import * as Styled from './styles';
import {ArticleMeta} from '../ArticleMeta';

export type ArticleHeaderProps = {
  title: string;
  capa: Image;
  articleMeta: ArticleMeta;
};

export const ArticleHeader = ({ title }: ArticleHeaderProps) => {
  return (
    <Styled.Wrapper>
      <h1>Oi</h1>
      <p>{title}</p>
    </Styled.Wrapper>
  );
};
